* {
  box-sizing: border-box;
}

nav {
  margin: 10px 0;
}

nav a {
  display: inline-block;
  margin: 10px;
  color: rgb(102, 102, 255);
  text-decoration: none;
}

.container {
  width: 80%;
  margin: 0 auto;
  background: rgb(221, 221, 221);
  padding: 20px;
}

h1 {
  color: blue;
}

h2 {
  color:rgb(102, 102, 255);
}

h3 {
  color: rgb(196, 85, 42);
}

.active {
  color: rgb(196, 85, 42);
}

input, textarea {
  flex-grow: 1;
  margin-left: 10px;
  padding: 7px;
  border: none;
  border-left: 1px solid grey;
}

label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  border: 1px solid grey;
  padding: 0 0 0 5px;
}

button {
  display: block;
  margin: 0 auto;
  padding: 10px;
  font-size: 16px;
  background: rgb(102, 102, 255);
  color: white;
  cursor: pointer;
  margin-top: 20px;
}

.postsList, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.postsList li {
  margin-bottom: 20px;
  border-bottom: 1px solid grey;
}

